<template>
    <div class="dropdown">
        <div class="button">
            <span class="d-none d-sm-block"></span>
            <div class="dropdown-img">
               <span>Cardápio</span>
            </div>
            <i class="fa fa-angle-down"></i>
        </div>
        <div class="dropdown-content">
            <router-link to="/home/pages" >
                Sugestão do Dia<MenuOne />
            </router-link>
            <router-link to="/home/pages/:id" >
                Café da Manhã<MenuTwo />
            </router-link>
            
        </div>
    </div>
</template>

<script>
import { userKey } from '@/global'
import { mapState } from 'vuex'
import Gravatar from 'vue-gravatar'

export default {
    name: 'Dropdown',
    components: { Gravatar },
    computed: mapState(['user']),
    methods: {
        logout() {
            localStorage.removeItem(userKey)
            this.$store.commit('setUser', null)
            this.$router.push({ name: 'auth' })
        }
    }
}
</script>

<style>

    .dropdown {
        position: relative;
        height: 50px;
        width: 95%;
        margin-top: 10px;
        margin-bottom: 50px;
    }

    .button {
        display: flex;
        align-items: center;
        color: #fff;
        font-weight: 100;
        height: 100%;
        padding: 0px 20px;
        background-color: #7c5a4e;
        border-radius: 2px;
    }

    .dropdown:hover {
        background-color: rgba(0, 0, 0, 0.2);
    }

    .dropdown-img {
        margin: 0px 10px;
    }

    .dropdown-img > img {
        max-height: 37px;
        border-radius: 2px;
        color: black;
    }


    .dropdown-content {
        position: absolute;
        right: 0px;
        background-color:#d2b48c;
        width: 100%;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        padding: 10px;
        z-index: 1;
        border-radius: 2px;

        display: flex;
        flex-direction: column;
        flex-wrap: wrap;

        visibility: hidden;
        opacity: 0;
        transition: visibility 0s, opacity 0.5s linear;
    }

    .dropdown:hover .dropdown-content {
        visibility: visible;
        opacity: 1;
        
    }

    .dropdown-content a {
        text-decoration: none;
        color: #000;
        padding: 10px;
    }

    .dropdown-content a:hover {
        text-decoration: none;
        color: #000;
        background-color: #EDEDED;
        border-radius: 10px;
    }
</style>
